import axios from "axios";
import { useEffect, useState } from "react";
import { Report } from "notiflix";

function SendMailToResetPassword() {
  const [email, setEmail] = useState("");

  const handleChangeMail = (evt) => {
    setEmail(evt.target.value);
  };


  const handleClickSendMail = (evt) => {
    evt.preventDefault();
    const login = {
      email
    };

    axios({
      method: "post",
      url: "http://localhost:8080/api/sessions/resetpassword",
      data: login,
    })
      .then((result) => {
        window.localStorage.setItem("jwtCookieToken", result.data);
        Report.success("Mail enviado", "");
      })
      .catch((error) => {
        if (error.response.status === 400) {
          Report.failure("Hubo un error, mail no enviado", "");
          console.error(error);
        }
      });
  };

  return (
    <>
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
        <div className="sm:mx-auto sm:w-full sm:max-w-sm">
          <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
            Enviar mail para renovar contraseña
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form className="space-y-6" onSubmit={handleClickSendMail}>
            <div>
              <label
                htmlFor="email"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Direccion Email
              </label>
              <div className="mt-2">
                <input
                  onBlur={handleChangeMail}
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-black px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Enviar mail
              </button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}

export default SendMailToResetPassword;

